// ====================================
// Customize breadcrumbs
// ====================================

/*
* TABLE OF CONTENTS
*
* TOOL ============
* mixin sidebar
* SIDEBAR STYLE ============
* sidebar spacer, background-color
* box-intro
* box-relate-site
* banner-ttmt
* box-file
* most-read
* search-file
* MAIN CONTAINER ============
* breaking-news
* above the fold
* media-zone
* box-style
* CATEGORY PAGE ============
*/

// > TOOL ============
.hidden {
    display: none !important;
}

#__bs_notify__ {
    display: none !important;
}

// > mixin sidebar

@mixin select-style() {
    height: 36px;
    border: 1px solid #ccc;
    background-color: #fff;
    border-radius: 3px;
    position: relative;
    @include caret(down);

    &:after {
        top: 16px;
        right: 10px;
        position: absolute;
    }

    select {
        border: none;
        height: 100%;
        line-height: 36px;
        font-size: 1.4rem;
        -webkit-appearance: none;
        -moz-appearance: none;
        padding-left: 10px;
        background-color: transparent;
        width: 100%;
    }

    &:hover {
        border: 1px solid #999;
    }
}

@mixin sidebar-title-style() {
    .box-heading {
        height: 35px;
        border-left: 5px solid $primary;
        padding-left: $spacer-4;
        font-size: 1.8rem;
        line-height: 35px;
        text-transform: uppercase;
        background-color: transparent;
        transition: all .2s linear;
        a {
            color: $primary;
            display: block;
        }
        &:hover{
            border-color: darken($primary, 10%);
            a{
                color: darken($primary, 10%);;
            }
        }
    }
}

// > SIDEBAR STYLE ============

// > sidebar spacer, background-color
.sub-column {
    >* {
        background-color: #fff;
        margin-top: 16px;

        &:first-child {
            margin-top: 0;
        }
    }
}

// > box-intro
.box-intro {
    padding-top: $spacer-4;
    @include sidebar-title-style;

    .box-content {
        padding: $spacer-3;
    }

    .item {
        @include clearfix();

        &__thumb {
            width: 110px;
            height: 120px;
            margin-right: $spacer-4;
            float: left;

            img {
                width: 100%;
                height: 100%;
            }
        }

        &__text {
            display: flex;
            flex-wrap: wrap;
            padding-top: $spacer-3;

            .name {
                font-size: 1.4rem;
                text-transform: uppercase;

                small {
                    display: block;
                }

                span {
                    font-weight: bold;
                }
            }

            .desc {
                margin-top: $spacer-2;
                font-size: 1.3rem;
                color: $gray-800;
            }
        }

        &:nth-child(2) {
            margin-top: $spacer-4;
        }
    }

    .btn-more{
        display: block;
        text-align: right;
        font-size: 1.2rem;
        margin-top: 10px;
        font-weight: bold;
        a{
            display: inline-block;
            padding: 5px 10px;
            border: 1px solid $gray-300;
            border-radius: 3px;
            &:hover{
                background-color: $gray-100;
            }
        }
    }
}

// > box-relate-site
.box-relate-site {
    padding-top: $spacer-4;
    @include sidebar-title-style;

    .box-title {}

    .box-content {
        padding: $spacer-4;

        .select-relate-site {
            @include select-style();
            height: 50px;
            border-radius: 10px;

            &:after {
                top: 22px;
            }

            select {
                width: calc(100% - 40px);
                margin-left: 40px;
                line-height: 50px;
                color: $gray-500;
                padding-left: 0;
            }

            i {
                position: absolute;
                top: 16px;
                left: 10px;
                font-size: 1.4em;
                color: $gray-600;
            }
        }

    }
}

.send-us {
    display: flex;
    margin-top: $spacer-4;
    height: 46px;
    width: 100%;
    border-radius: 23px;
    background-color: $primary;
    justify-content: center;
    align-items: center;
    line-height: 46px;
    font-size: 1.6rem;
    text-transform: uppercase;
    color: #fff;

    i {
        margin-right: 10px;
    }

    span {
        padding-top: 4px;
    }

    &:hover {
        background-color: darken($primary, 10%);
        color: #fff;
    }
}

// > banner-ttmt
.mattran-info {
    padding-top: $spacer-4;
    @include sidebar-title-style;


    .box-heading {
        font-size: 1.4rem;
        padding-left: 1rem;
    }

    .box-content {
        padding: $spacer-4;

        .mattran-search {
            border: 1px solid $gray-300;
            border-radius: 10px;
            overflow: hidden;

            button,
            input {
                border: none;
            }

            button {
                width: 40px;
                height: 50px;
                text-align: center;
            }

            input {
                width: calc(100% - 50px);
                height: 50px;
                line-height: 50px;
                vertical-align: top;
                padding-left: $spacer-3;
            }

            i {
                font-size: 1.6rem;
            }
        }

        .mattran-list {
            padding-top: $spacer-4;
            font-size: 1.4rem;

            .title {}

            .wrap {
                height: 140px;
                overflow: auto;
            }

            a {
                display: block;
                margin-top: $spacer-4;

                i {
                    font-size: 1.4em;
                    color: $gray-600;
                    margin-right: $spacer-3;
                    vertical-align: top;
                }

                span {
                    display: inline-block;
                    line-height: 20px;
                    color: #2795E1;
                    font-weight: bold;
                }
            }
        }
    }
}

// > box-file
.box-file {
    padding-top: $spacer-4;
    @include sidebar-title-style;

    .box-content {
        padding: $spacer-4;
    }

    .panel-wrap {
        @include clearfix();
        border: 1px solid $gray-300;
    }

    .panel-title {
        display: flex;
        background-color: $gray-300;
        border-bottom: 1px solid $gray-300;

        a {
            height: 30px;
            padding: 0 $spacer-4;
            line-height: 30px;
            color: $gray-600;
            font-size: 1.2rem;
            text-transform: uppercase;
            font-weight: bold;
            border-left: 1px solid $gray-300;

            &:first-child {
                border-left: none;
            }

            &:hover,
            &.is-active {
                background-color: #fff;
                color: $gray-800;
            }
        }
    }

    .panel-content {
        padding-left: $spacer-4;
        padding-bottom: $spacer-4;
        .panel{
            display: none;
            &.is-active{
                display: block;
            }
        }

        .story {
            &__heading {
                display: flex;
                justify-content: space-between;
                margin-top: $spacer-3;
                font-weight: bold;
                @include font-style(12, 16);

                i {
                    float: left;
                    margin-top: 2px;
                    font-size: 1rem;
                    margin-right: $spacer-3;
                    color: $primary;
                }

                a {
                    display: inline-block;

                }
            }
        }
    }
}

// > most-read
.most-read {
    padding-top: $spacer-4;
    @include sidebar-title-style;

    .box-content {
        padding: $spacer-4;
    }

    .panel-wrap {
        @include clearfix();
        border: 1px solid $gray-300;
    }

    .panel-title {
        display: flex;
        background-color: $gray-300;
        border-bottom: 1px solid $gray-300;

        a {
            height: 30px;
            padding: 0 $spacer-4;
            line-height: 30px;
            color: $gray-600;
            font-size: 1.2rem;
            text-transform: uppercase;
            font-weight: bold;
            border-left: 1px solid $gray-300;

            &:first-child {
                border-left: none;
            }

            &:hover,
            &.is-active {
                background-color: #fff;
                color: $gray-800;
            }
        }
    }

    .panel-content {
        padding-left: $spacer-4;
        padding-bottom: $spacer-4;
        .panel{
            display: none;
            &.is-active{
                display: block;
            }
        }

        .story {
            &__heading {
                display: flex;
                justify-content: space-between;
                margin-top: $spacer-3;
                font-weight: bold;
                @include font-style(12, 16);

                i {
                    float: left;
                    margin-top: 2px;
                    font-size: 1rem;
                    margin-right: $spacer-3;
                    color: $primary;
                }

                a {
                    display: inline-block;

                }
            }
        }
    }
}

// > search-file
.search-file {
    background-color: $gray-200;
    padding-bottom: 10px;

    .title {
        height: 34px;
        line-height: 34px;
        padding-left: .5rem;
        font-size: 1.6rem;
        font-weight: bold;
    }

    .content {

        .select-file-type-1,
        .select-file-type-2 {
            @include select-style();
        }

        .wrap-input {
            margin: 0px 5px 0;
            height: 34px;
            line-height: 34px;

            &:after {
                display: none;
            }

            input {
                width: 100%;
                height: 100%;
                padding-left: 10px;
            }
        }

        .form-group {
            display: flex;
            align-items: center;
            padding-left: 7px;
        }

        .col-form-label {
            margin-bottom: 0;
            width: 70px;
        }

        .select-file-type-1,
        .select-file-type-2 {
            width: calc(100% - 70px);
            margin-right: 5px;
        }

        .select-file-type-2 {
            width: calc(100% - 170px);
        }

        >* {
            margin-top: $spacer-3;
        }

        .button-action {
            width: 75px;
            height: 36px;
            display: block;
            background-color: $primary;
            color: #fff;
            font-size: 12px;
            font-weight: bold;
            margin-left: -15px;
            text-transform: uppercase;
            float: right;
            flex: flex-end;
        }
    }
}

.box-style-4 {
    background-color: #fff;
    margin-top: 20px;
    padding-top: $spacer-4;
    @include sidebar-title-style;

    .box-heading {
        font-size: 1.7rem;
    }

    .box-content {
        padding: $spacer-4;

        .rank-1 {
            .story {
                &__thumb {}

                &__heading {
                    @include font-style(20, 26);
                }

                &__summary {
                    margin-top: $spacer-2;
                }
            }
        }

        .rank-2 {
            .story {
                margin-top: $spacer-3;
                border-top: 1px solid $gray-200;
                padding-top: $spacer-3;

                &:first-child {}

                &__heading {
                    @include font-style(14, 20);
                }

                &:not(:first-child) {
                    margin: 10px 0px 0;
                }
            }

        }
    }

}

// > MAIN CONTAINER ============
// > breaking-news
.breaking-news {
    height: 54px;
    border-bottom: 1px solid #B9C8BF;
    overflow: hidden;

    .box-content {
        padding: 0 0 0 $spacer-4;
    }

    .text-title {
        float: left;
        display: flex;
        align-items: center;
        height: 54px;
        width: 60px;
        color: $primary;
        text-transform: uppercase;
        font-weight: bold;
    }

    .news-slider {
        float: left;
        width: calc(100% - 60px);

        a {
            display: flex;
            align-items: center;
            font-style: italic;
            font-size: 1.3rem;
        }

        .story {
            background-color: #fff;
            padding: 10px 10px 0px 20px;
            margin-left: 1px;
            width: 300px;
            height: 53px;
            float: left;

            .story__thumb {
                width: 40px;
                height: 30px;
                @include runaround(left, 2, 10, 0, 0);
            }

            .story__heading {
                font-style: italic;
                @include font-style(12, 16);
            }
        }
    }
}

.global-time {
    position: absolute;
    top: -20px;
    font-size: 1.2rem;
}

// > above the fold
.rank1-stories,
.rank2-stories,
.rank3-stories {
    background-color: #fff;
    padding: 16px;
}

.rank1-stories {
    height: 470px;
    overflow: hidden;

    .story {
        &__thumb {
            margin-left: -8px;
            margin-top: -8px;
            margin-right: -8px;

            .story__cate {
                background-color: rgba($secondary, .8);
            }
        }

        &__heading {
            @include font-style(24, 32);
        }

        &__summary {
            margin-top: $spacer-2;
        }

        &__refer {
            border-top: 1px solid $gray-200;
            margin-top: $spacer-2;
            padding-top: $spacer-2;
        }
    }
}

.rank2-stories {
    padding: 8px;
    padding-right: 0;
    height: 470px;
    overflow: hidden;

    .slimScrollDiv {
        padding-right: 8px;
    }

    .slimScrollBar {
        width: 3px !important;
        opacity: .2 !important;
        right: 3px !important;

        .wrap {
            overflow: scroll;
        }
    }

    .story {
        margin-top: $spacer-3;
        border-top: 1px solid $gray-200;
        padding-top: $spacer-3;

        &:first-child {}

        &__heading {
            @include font-style(14, 20);
        }

        &:not(:first-child) {
            margin: 10px 8px 0;
        }
    }

    .story:first-child {
        margin-top: 0;
        border-top: none;
        padding-top: 0;

        .story__heading {
            margin: 10px 8px 0;
        }
    }
}

.rank3-stories {
    padding: 10px;
    display: flex;
    justify-content: space-between;
    margin-top: 16px;

    .story {
        margin-left: 10px;

        &__heading {
            font-weight: bold;
        }

        &:first-child {
            margin-left: 0;
        }
    }
}

.timeline-event{
    max-height: 500px;
    overflow: hidden;
    position: relative;
    .bx-controls {
        position: absolute;
        top: -45px;
        right: 10px;

        .bx-pager-item {
            float: left;
            margin-left: 10px;
        }

        .bx-pager-link {
            display: block;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            opacity: .5;
            background-color: $primary;
            text-indent: -999em;

            &.active {
                opacity: 1;
            }
        }
    }
    .fyi {
        margin-top: 10px;
        text-align: center;
    }
}

// > media-zone
.media-zone {
    @include clearfix();
    padding: 20px;
    margin-top: 30px;
    background: #CC0001 url(../img/trongdong-video-bg-min.png) center top no-repeat;

    .col-main {
        width: 690px;
        float: left;

        .fyi {
            margin-top: 30px;
            text-align: center;
        }
    }

    .col-sub {
        width: 280px;
        float: right;
    }

    .rank1-media,
    .rank2-media,
    .rank3-media,

        {
        .story {
            .story__heading {
                a {
                    color: #fff;
                }
            }

            .story__thumb {
                position: relative;

                .fa-play-circle {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    z-index: 10;
                    color: #fff;
                    font-size: 4rem;
                }
            }

            .story__summary {
                color: #fff;
            }
        }
    }

    .rank1-media {
        .story {
            .story__thumb {
                height: 370px;

                .fa-play-circle {
                    font-size: 8rem;
                }
            }

            &__heading {
                @include font-style(24, 32);
            }

            &__summary {
                margin-top: $spacer-2;
            }
        }
    }

    .rank2-media {
        display: flex;
        justify-content: space-between;
        margin-top: 16px;

        .story {
            margin-left: 15px;

            &__heading {
                font-weight: bold;
            }

            &:first-child {
                margin-left: 0;
            }
        }
    }

    .rank3-media {
        @include sidebar-title-style;

        .box-heading {
            border-left: 8px solid #fff;

            a {
                color: #fff;
            }
        }

        .box-content {
            padding-top: $spacer-5;
        }

        .story {
            margin-top: $spacer-5;

            &__heading {
                font-weight: bold;
            }

            &:first-child {
                margin-top: 0;
            }
        }
    }
}

// > box-style
.box-style {
    @include clearfix();

    .sub-column {
        margin-top: 20px;
    }
}

.box-style-1 {
    @include sidebar-title-style;
    background-color: #fff;
    padding: 20px;
    margin-top: 20px;

    .box-title {}

    .box-content {
        margin-top: 20px;

        .rank-1 {
            width: 350px;
            float: left;

            .story {
                &__thumb {}

                &__heading {
                    @include font-style(24, 32);
                }

                &__summary {
                    margin-top: $spacer-2;
                }
            }
        }

        .rank-2 {
            width: 280px;
            float: right;

            .story {
                margin-top: 16px;

                &__thumb {
                    width: 140px;
                    height: 80px;
                    @include runaround(left, 0, 10, 0, 0);
                }

                &__heading {
                    @include font-style(14, 20);
                    font-weight: bold;
                }

                &:first-child {
                    margin-top: 0;
                }
            }
        }
    }
}

.box-style-2 {
    @include sidebar-title-style;
    background-color: #fff;
    padding: 20px;
    margin-top: 20px;

    .box-title {}

    .box-content {
        margin-top: 20px;

        .rank-1 {
            .story {
                &__thumb {
                    width: 350px;
                    height: 211px;
                    @include runaround(left, 0, 20, 0, 0);
                }

                &__heading {
                    @include font-style(24, 32);
                }

                &__summary {
                    margin-top: $spacer-3;
                }
            }
        }

        .rank-2 {
            display: flex;
            justify-content: space-between;
            margin-top: 20px;

            .story {
                margin-left: 16px;

                &__heading {
                    font-weight: bold;
                }

                &:first-child {
                    margin-left: 0;
                }
            }
        }
    }
}

.box-style-3 {
    @include sidebar-title-style;
    background-color: #fff;
    padding: 20px;
    margin-top: 20px;

    .box-title {}

    .box-content {
        margin-top: 20px;

        .rank-1 {
            .story {
                height: 370px;
                width: 650px;
                position: relative;

                &__thumb {
                    position: absolute;
                    width: 650px;
                    height: 370px;
                }

                &__heading {
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#000000+0,000000+45,000000+100&0+0,1+100 */
                    background: -moz-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 45%, rgba(0, 0, 0, 1) 100%);
                    /* FF3.6-15 */
                    background: -webkit-linear-gradient(top, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 45%, rgba(0, 0, 0, 1) 100%);
                    /* Chrome10-25,Safari5.1-6 */
                    background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 45%, rgba(0, 0, 0, 1) 100%);
                    /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
                    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#00000000', endColorstr='#000000', GradientType=0);
                    /* IE6-9 */
                    padding: 20px;
                    padding-top: 220px;
                    @include font-style(24, 32);

                    a {
                        color: #fff;
                    }
                }

            }
        }

        .rank-2 {
            display: flex;
            justify-content: space-between;
            margin-top: 20px;

            .story {
                margin-left: 16px;

                &__heading {
                    font-weight: bold;
                }

                &:first-child {
                    margin-left: 0;
                }
            }
        }
    }
}

// > CATEGORY PAGE ============
.category-container {
    .box-style-1{
        margin-top: 0;
        .box-content{
            margin-top: 0;
        }
    }
}

.list-news {
    background-color: #fff;
    padding: 20px;
    border-top: 1px solid $gray-300;

    .story {
        border-top: 1px solid $gray-200;
        margin-top: 16px;
        padding-top: 16px;

        &__thumb {
            width: 220px;
            height: 120px;
            @include runaround(left, 0, 10, 0, 0);
        }

        &__heading {
            @include font-style(18, 24);
            font-weight: bold;
        }

        &:first-child {
            margin-top: 0;
            padding-top: 0;
            border-top: none;
        }
    }
}