/*
* TABLE OF CONTENTS
*
* header +
*/

// > header
.site-header {
    &__top {
        height: 40px;
        background-color: #fff;
        border-bottom: 1px solid $gray-500;
        font-size: 1.2rem;

        div[class*="col-"] {
            height: 40px;
            display: flex;
            align-items: center;
        }

        .info {
            line-height: 4rem;
            display: block;
            width: 150px;
        }

        .link {
            display: flex;
            justify-content: flex-start;
            width: 100%;

            a {
                display: inline-block;
                margin-left: $spacer-5;

                &:first-child {
                    // margin-left: $spacer-4;
                    margin-left: 0;
                }
            }
        }

        .col-4 {
            justify-content: flex-end;
        }

        .header-search {

            button,
            input {
                border: none;
            }

            button {
                width: 40px;
                height: 40px;
                text-align: center;
            }

            input {
                width: 200px;
                text-align: right;
                height: 38px;
                line-height: 40px;
                vertical-align: top;
                padding-right: $spacer-3;
            }

            i {
                font-size: 1.6rem;
            }
        }
    }

    &__body {
        height: 170px;
        background: #CC0001 url(../img/header-bg.png) center 0 no-repeat;

        .logo {
            width: 244px;
            height: 181px;
            background: url(../img/logo-mattran.png) center center no-repeat;
            margin: -30px auto;
            a{
                display: block;
                width: 100%;
                height: 100%;
            }
        }

        .social-link {
            position: absolute;
            top: 70px;
            right: 10px;
            display: flex;
            justify-content: space-between;

            a {
                position: relative;
                display: flex;
                justify-content: flex-start;
                align-items: center;
                flex-direction: column;
                width: 70px;
                height: 60px;
                margin: 0 6px;
                padding-top: 10px;
                border-radius: 5px;
                color: #fff;
                font-size: 1.2rem;
                background-color: transparent;
                transition: all .2s linear;
                &:before{
                    content: '';
                    width: 1px;
                    height: 60px;
                    position: absolute;
                    left: -5px;
                    top: 0;
                    background-color: rgba(white, .5);
                }
                &:hover{
                    background-color: rgba(white, .2);
                }
                &:first-child{
                    border-left: none;
                    &:before{
                        content: none;
                    }
                }

                i {
                    display: block;
                    margin-bottom: $spacer-3;
                    font-size: 1.6rem;
                }
            }
            // a {
            // 	display: block;
            //     color: #fff;
            //     text-align: center;
            //     width: 100px;
            //     border-left: 1px solid rgba(white, .5);
            //     font-size: 1.2rem;
            //     &:first-child{
            //     	border-left: none;
            //     }

            //     i {
            //         display: block;
            //         margin-bottom: $spacer-3;
            //         font-size: 1.6rem;
            //     }
            // }
        }
    }
}