// ====================================
// Article page's style
// ====================================

/*
* TABLE OF CONTENTS
*
* normal article body
* sapo
* grid article
* header article
*/

// Note: wrap all article'elements exept sidebar
.article-container {
    @include clearfix();
    .main-column{
        background-color: #fff;
        padding: 20px;
    }
}

/* > normal article body*/
.article {
    @include clearfix();
    position: relative;

    &__header {
        @include clearfix();
        padding-top: 15px;
        padding-bottom: 15px;
        text-transform: uppercase;

        h1 {
            @include font-style(40, 50);
            font-family: $font-family-sans-serif;
            margin-bottom: 10px;
        }
    }

    &__meta {
        .spr--comment {
            background-position: 0 -2150px;
        }

        float: left;
        border-top: 1px solid #000;
        padding-top: 15px;
    }

    &__meta {
        font-size: 0;
        vertical-align: middle;
        overflow: hidden;
        font-size: 0;
        @include clearfix();

        >* {
            margin-right: 8px;
            padding-right: 8px;
            float: left;
            position: relative;
            @include font-style(12, 22);

            &:after {
                content: '';
                width: 1px;
                height: 12px;
                top: 4px;
                right: 0;
                position: absolute;
                background-color: #000;
            }

            &:last-child {
                padding-right: 0;
                margin-right: 0;

                &:after {
                    display: none;
                }
            }
        }

        span,
        a {
            vertical-align: top;
            display: inline-block;

            &:hover {
                color: $primary;
            }
        }

        .author {
            a {
                color: #DE0202;
                font-weight: bold;
                margin-left: 3px;
            }
        }

        .comment {
            i {
                vertical-align: top;
                margin-top: 2px;
            }
        }
    }

    &__sapo {
        @include font-style(20, 28);
        margin-bottom: 10px;
    }

    &__inner-relate {
        padding: 10px;

        @include media-breakpoint-up(sm) {
            padding: 10px 20px;
        }

        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        margin-bottom: 10px;

        .story {
            margin-bottom: 5px;

            .story__heading {
                @include media-breakpoint-up(xs) {
                    @include font-style(14, 20);
                }

                @include media-breakpoint-up(sm) {
                    @include font-style(13, 20);
                }

                margin: 0;
                padding-top: 0px;
                padding-left: 10px;
                position: relative;

                &:before {
                    content: "•";
                    display: inline-block;
                    font-size: 14px;
                    position: absolute;
                    left: 0;
                    top: 0px;
                    color: $gray-300;
                }
            }

        }
    }

    &__content-img {
        display: block;
        margin-bottom: 10px;

        a.photo {
            position: relative;
            margin-bottom: 5px;
            display: block;

            i,
            em {
                position: absolute;
                bottom: 0;
                left: 0;
            }

            img {
                max-width: 100%;
                height: auto;
                margin-left: auto;
                margin-right: auto;
            }
        }

        figcaption.fig {
            @include font-style(14, 18);
            font-style: italic;
            color: #777;
            text-align: center;
            margin-top: 5px;
        }

        &.full {
            width: 100%;

            figcaption.fig {
                text-align: center;
            }
        }

        &.left {
            width: 50%;
            float: left;
            margin-right: 20px;

            figcaption.fig {
                text-align: left;
            }
        }

        &.right {
            width: 50%;
            float: right;
            margin-left: 20px;

            figcaption.fig {
                text-align: right;
            }
        }
    }

    &__quote {
        color: #fff;
        padding: 16px;
        margin: 5px 20px 10px 0px;
        width: 40%;
        background-color: $primary;
        float: left;
        position: relative;
        > i{
            position: absolute;
            top: 16px;
            left: 16px;
            font-size: 60px;
            color: #fff;
        }
        .text {
            text-indent: 70px;
            padding-top: 40px;
            display: inline-block;
            position: relative;

            &:before {
                background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                background-size: ($sprite-w + px) ($sprite-h + px);
                display: inline-block;
                vertical-align: middle;
                background-position: 0 -2000px;
                content: '';
                width: 60px;
                height: 60px;
                position: absolute;
                top: 0px;
                left: 0px;
            }
        }

        .author {
            font-style: normal;
            text-align: left;
            font-weight: bold;
            display: block;
            padding-top: 25px;
            position: relative;

            &:before {
                background-color: #fff;
                content: '';
                width: 60%;
                height: 1px;
                position: absolute;
                top: 10px;
                left: 0px;
            }
        }
    }

    &__notice {
        background-color: #d9edf7;
        border-color: #bce8f1;
        color: #31708f;
        padding: 16px;
    }

    &__author {
        text-align: right;
        font-weight: bold;
    }

    &__social {
        @include clearfix;
        font-size: 0;
        margin-bottom: 10px;
        z-index: 90;

        ul {
            float: left;
            margin-right: 5px;

            li {
                float: left;
                margin-right: 5px;
            }
        }

        .tool {
            float: right;
            display: block;

            a {
                height: 20px;
                line-height: 20px;
                border-radius: 3px;
                display: inline-block;
                padding: 0 5px;
                font-size: 11px;
                color: #fff;

                i {
                    margin-right: 5px;
                }
            }

            a.print {
                background-color: #999;

                @include media-breakpoint-up(xs) {
                    display: none;
                }
            }
        }
    }

    &__tag {
        border-top: 1px solid #ddd;
        border-bottom: 1px solid #ddd;
        margin-bottom: 20px;
        padding: 10px 0;
        @include clearfix;

        .title {
            @include font-style(15, 30);
            padding: 0 15px 0 0;
            display: inline-block;
            text-transform: uppercase;
            float: left;
            margin-top: 5px;
        }

        p {
            @include font-style(15, 20);
            margin-left: 65px;
            padding-top: 4px;

            a {
                border-radius: 15px;
                display: inline-block;
                margin-bottom: 5px;
                padding: 5px 10px;
                border-radius: 3px
            }
        }
    }

    &__body {
        // Note: padding, margin, fontstyle among article body
        overflow: hidden;
        overflow-wrap: break-word;
        @include clearfix();
        @include font-style(16, 24, normal);

        p,
        div,
        h3,
        h4,
        h5 {
            margin-bottom: 15px;
        }

        ins {
            text-decoration: none;
        }

        img {
            max-width: 100%;
            display: block;
            margin: auto;
            transform: translate3d(0, 0, 0);
        }

        table {
            margin: auto;
            max-width: 400px;
        }

        .body-image {
            text-align: center;

            .photo {
                display: inline-block;
                position: relative;

                &:after {
                    background-image: url('../img/desktop-sprite-'+$sprite-version+'.png');
                    background-color: rgba(0, 0, 0, .5);
                    background-position: -0px -2900px;
                    vertical-align: middle;
                    background-size: ($sprite-w + px) ($sprite-h + px);
                    position: absolute;
                    top: 5px;
                    right: 5px;
                    content: '';
                    width: 44px;
                    height: 44px;
                    border-radius: 50%;
                }
            }

            .photo:hover {
                &:after {
                    background-color: rgba(47, 161, 179, .5);
                }
            }
        }

        >* {
            max-width: 100%;
        }
    }

    &__comment {
        margin-bottom: 20px;
        @include clearfix;

        >.title {
            @include font-style(28, 34);
            margin-bottom: 10px;
            display: block;
        }

        .user {
            margin-bottom: 10px;
        }

        .comment {
            border-bottom: 1px solid #eee;
            margin-bottom: 10px;
            padding-bottom: 10px;
        }

        .avatar {
            height: 25px;
            width: 25px;
            border-radius: 50%;
            border: 1px solid #ccc;
            display: inline-block;
            float: left;
            position: relative;

            i {
                font-size: 12px;
                color: #ccc;
                position: absolute;
                top: 5px;
                left: 7px;
            }
        }

        .name {
            @include font-style(13, 18);
            text-transform: uppercase;
            margin-bottom: 5px;
        }

        .text {
            @include font-style(15, 20);
            color: #555;
            margin-bottom: 5px;
        }

        .tool {
            a {
                font-size: 12px;
                color: #777;
                display: inline-block;
                margin-right: 10px;

                &:hover {
                    color: $primary;
                }

                i {
                    font-size: 14px;
                    margin-right: 5px;
                }
            }
        }

        .reply {
            padding: 10px 0 0 25px;
            text-align: right;

            textarea {
                border-radius: 3px;
                border: 1px solid #ddd;
                width: 100%;
                height: 50px;
                padding: 5px;
                font-size: 15px;
                line-height: 20px;
            }

            .btnSubmit {
                display: inline-block;
                margin-bottom: 0;
                font-weight: 400;
                text-align: center;
                touch-action: manipulation;
                cursor: pointer;
                border: 1px solid transparent;
                white-space: nowrap;
                padding: 3px 5px;
                font-size: 15px;
                line-height: 1.42857;
                border-radius: 0;
                -ms-user-select: none;
                user-select: none;
                border-radius: 3px;
                background-color: $primary;
                color: #fff;
                font-size: 12px;
                line-height: 1.5;

                &:hover {
                    background-color: darken($primary, 10%);
                }

                &.disabled {
                    cursor: default;
                }
            }
        }

        .sub-comments {
            margin-top: 10px;
            padding: 32px;
            margin-left: 25px;
            background-color: rgba($primary, .1);
        }

        .input {
            @include clearfix;

            textarea {
                border-radius: 3px;
                border: 1px solid #ddd;
                width: 100%;
                height: 70px;
                padding: 10px;
            }

            p.note {
                font-style: italic;
                font-size: 11px;
                float: left;
                display: inline-block;
                color: #999;

                @include media-breakpoint-up(xs) {
                    line-height: 17px;
                    width: 60%;
                }

                @include media-breakpoint-up(sm) {
                    line-height: 31px;
                }
            }

            button {
                border: none;
                border-radius: 3px;
                float: right;
                background-color: $primary;
                color: #fff;
                @include font-style(14, 32);
                padding-left: 10px;
                padding-right: 10px;

                &:hover {
                    background-color: darken($primary, 10%);
                }
            }
        }
    }

    &__hot-video {
        margin-bottom: 30px;

        .title {
            @include font-style(20, 55);
            background-color: #f5f5f5;
            border-top: 1px solid #77c2af;
            text-transform: uppercase;
            padding-left: 15px;
        }

        .content {
            overflow: hidden;
            position: relative;

            @include media-breakpoint-up(sm) {
                background-color: #f5f5f5;
                text-align: center;
            }
        }
    }
    
    &__more-news{
        @include sidebar-title-style;
        background-color: #fff;
        margin-top: 30px;
        .box-title {}

        .box-content {
            .rank-2 {
                display: flex;
                justify-content: space-between;
                margin-top: 10px;

                .story {
                    margin-left: 16px;

                    &__heading {
                        font-weight: bold;
                    }

                    &:first-child {
                        margin-left: 0;
                    }
                }
            }
        }

    }
}

