/*
* TABLE OF CONTENTS
*
*/

.site-footer{
    background-color: #fff;
    padding: $spacer-5 0;
    margin-top: $spacer-5;
    .copy-right{
        h2{
            margin-bottom: $spacer-3;
            text-transform: uppercase;
            @include font-style(18,24);
            color: $primary;
            small{
                display: block;
            }
        }
        p{
            @include font-style(12,20, normal);
            color: $gray-700;
        }
    }
    .box-link{
        border-left: 1px solid $gray-500;
        padding-left: $spacer-4;
        margin-top: $spacer-3;
        .box-heading{
            text-transform: uppercase;
            @include font-style(14,20);
        }
        .box-content{
            li{
                margin-top: $spacer-2;
            }
            a{
                @include font-style(12,20, normal);
            }
        }
    }
}